.header{
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  top: 40px;
  .wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  &__logo{
    width: em(158);
  }
  &__menu{
    list-style: none;
    display: flex;
    &-item{
      margin-left:em(35);
      @media screen and (max-width: $sm){
        margin-left:em(15);
      }
    }
    a{
      color:black;
      font-size: em(20);
    }
  }
}
.welcome{
  background: #F0DC95;
  position: relative;
  overflow: hidden;
  padding:em(209) 0 em(201);
  @media screen and (max-width: $sm){
    padding:em(180) 0 em(101);
  }
  &__content{
    width: em(580);
    margin:0 auto;
    position: relative;
    left: em(30);
    @media screen and (max-width: $sm){
      width: em(500);
      margin: 0;
      position: static;
    }
  }
  &__title{
    font-family: 'Raleway';
    font-size: em(95);
    line-height: 1.05em;
    margin-bottom:em(3.4);
    @media screen and (max-width: $sm){
      font-size: em(70);
    }
  }
  &__desc{
    font-size: em(22);
    line-height: 1.4em;
  }
  &__img{
    position: absolute;
    top:0;
    bottom:0;
    margin:auto;
    width: em(380);
    transform:translateY(em(50));
    @media screen and (max-width: $md){
      top:auto;
      bottom:em(100);
    }
    @media screen and (max-width: $sm){
      bottom:em(30);
    }
    &.-left{
      left: 0;
      @media screen and (max-width: $md){
        left: em(-140);
      }
      @media screen and (max-width: $sm){
        display: none;
      }
    }
    &.-right{
      right:0;
      @media screen and (max-width: $md){
        right: em(-140);
      }
      @media screen and (max-width: $sm){
        right: em(-100);
      }
    }
  }
}
.catalog{
  padding:em(82) 0 em(79);
  &__list{
    height:em(830);
    overflow: hidden;
    transition:all .2s ease;
    &-wrap{
      display: flex;
      flex-wrap: wrap;
      margin:0 em(-8.5);
    }
    &-item{
      min-width: 25%;
      padding:em(8.5) em(8.5);
      @media screen and (max-width: $sm){
        min-width: 50%;
      }
    }
    &-img{
      display: block;
      padding-top: 100%;
      position: relative;
      margin:0 0 em(15);
      overflow: hidden;
      &:hover{
        img{
          transform:scale(1.2);
        }
      }
      img{
        position: absolute;
        top: 0;
        bottom: 0;
        max-width: auto;
        width: auto;
        height: 100%;
        margin: auto;
        left: 0;
        right: 0;
        transition:all .2s ease;
      }
    }
    &-title{
      font-size: em(20);
      font-weight: 500;
      color:black;
    }
    &-description{
      position: relative;
    }
    &-price{
      font-size: em(20);
      font-weight: 500;
      position: absolute;
      top:em(1);
      right:em(5);
      color:#454DA3;
    }
    &-desc{
      font-size: em(12);
      font-weight: 500;
      opacity: .4;
    }
  }
  &__btn{
    text-align: center;
    padding-top: em(35);
    &-link{
      display: inline-block;
      font-size: em(12);
      font-weight: 500;
      margin:0 auto 0;
      padding-top:em(20);
      color:rgba(0,0,0,0.4);
      position: relative;
    }
    
    &-icon{
      width: em(60);
      position: absolute;
      top:em(-35);
      left:0;
      right:0;
      margin:auto;
    }
  }
  &__heading{
    font-size: em(32);
    font-weight: 500;
    color:black;
    margin-bottom: em(10);
  }

}
.benefits{
  background: #A8AFF4;
  padding:em(50) 0 em(100);
  overflow: hidden;
  &__heading{
    font-size: em(32);
    font-weight: 500;
    color:white;
    margin-bottom: em(55);
  }
  .swiper{
    margin:0 em(-320);
    @media screen and (max-width: $sm){
      margin:0 em(-520);
    }
  }
  .swiper-slide{
    padding:0 em(30);
    opacity: .6;
    transition:all .2s ease;
    &.swiper-slide-active{
      opacity: 1;
    }
  }
  &__item{
    background: white;
    padding:em(20) em(60) em(40);
    box-sizing:border-box;
    text-align: center;
  }
  &__title{
    font-size: em(22);
    font-weight: 500;
    line-height: 1.4em;
    margin-bottom: em(4);
  }
  &__desc{
    font-size: em(16);
    margin-bottom: em(22);
  }
  &__img{
    width: em(243);
    margin:0 auto 0;
  }
}
.interiors{
  padding:em(90) 0 em(100);
  &__heading{
    font-size: em(32);
    font-weight: 500;
    color:black;
    margin-bottom: em(20);
  }
  &__grid{
    column-count: 3;
    column-gap: 25px;
    font-size: 0;
    transition: all .3s ease-in-out;
    column-fill: initial;
    @media screen and (max-width: $sm){
      column-count: 2;
      column-gap: 5px;
    }
    
    &-item{
      display: inline-block;
      vertical-align: top;
      width: 100%;
      background-size:cover;
      background-position: 50%;
      margin-bottom: 25px;
      @media screen and (max-width: $sm){
        margin-bottom: 5px;
      }
      &:nth-child(6) ~ div{
        display: none;
      }
    }
  }
  &__btn{
    text-align: center;
    margin-top: em(20);
  }
  &__btn-b{
    &.loaded{
      display: none;
    }
  }
}
.btn{
  display: inline-block;
  color:white;
  font-size: em(20);
  padding:em(15) em(42);
  cursor: pointer;
  transition:all .2s ease;
  &__main{
    background: #454DA3;
    position: relative;
    &:hover{
      background: #272d73;
    }
  }
  &__large{
    background: #959DF0;
    position: relative;
    color: black;
    padding:em(20) em(100);
    &:hover{
      background: #D2D5F4;
    }
  }
}
.footer{
  color: white;
  background: #0B1152;
  padding:em(54) 0 em(52);
  .wrap{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    min-width: 25%;
    @media screen and (max-width: $sm){
      min-width: 50%;
      margin-bottom: em(30);
    }
  }
  &__title{
    font-size: em(25);
    font-weight: 600;
    margin-bottom: em(12);
  }
  &__social{
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    li{
      min-width: em(40);
      margin-right: em(25);
    }
  }
  &__text{
    font-size: em(18);
    margin-bottom: em(10);
  }
  &__list{
    list-style: none;
    padding: 0;
    margin: 0;
    li{
      padding: 0;
      margin-bottom: em(12);
    }
    a{
      color:white;
      font-size: em(18);
      font-weight: 300;
    }
  }
}